@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');



*{
    margin: 0;
    padding:0;
}



body{
    background-color: #333;
    color: white;
    font-family: "Montserrat", sans-serif;
    font-weight: 300;
}

.header{
    background-color: #222;
    padding: 25px 100px;
    font-weight: 600;
}

main{
    margin: 50px 100px;
    width: 50%;
    float: left
}

aside {
    width: 20%;
    margin-top: 50px;
    margin-left: 5%;
    margin-right: 10%;
    float: right;
}


main .user{
    margin-bottom: 50px;
    background-color: #3e8fa0;
    padding: 20px;
}

main .delete-icon{
    float: right;
    color: #134b57;
    cursor: pointer;
    transform: scale(2);
}

main .edit-icon{
    float: right;
    color: #165664;
    cursor: pointer;
    transform: scale(2);
    margin-right: 25px;
}

main .delete-icon:hover,
main .edit-icon:hover{
    opacity: 0.5;
}


form input:not([type="checkbox"]),
form textarea,
form button{
    width: 90%;
    padding: 15px 5%;
    font-family: "Montserrat", sans-serif;
    font-weight: 300;
    margin-bottom: 10px;
    background: #222;
    color: #fff;
    outline: none;
    border: none;
    font-size: 15px;
}


form input[type="checkbox"]{
    margin: 10px;
}

form button{
    width: 100%;
    cursor: pointer;
    transition: transform 500ms ease;
}

form button:hover {
    transform: scale(1.1);
}

.user form {
    margin-top: 20px;
}

.user form input,
.user form textarea,
.user form button{
    background-color: #134b57;
}

.user form button:hover {
    transform: scale(1.01);
}

img {
    float: right;
    margin-top: -20px;
}